@media print {
  * {
    font-family: "Arial", serif;
    font-weight: normal;
  }
  @page {
    /* Ukuran kertas */
    size: 21.5cm 14cm; /* Lebar */
    margin: 0.5cm 0.1cm 0 0.25cm; /* Left */
  }
  body {
    font-size: 11pt;
  }
  body .table {
    margin: 0;
    width: 100%;
    border-collapse: collapse;
    font-size: 11pt;
  }
  body .table th {
    text-align: left;
  }
  body .table th, body .table td {
    padding: 1pt 2pt;
    border: 1pt solid black;
  }
  body .table td {
    letter-spacing: 0.05em;
    vertical-align: top;
  }
  body .table thead tr th {
    text-align: left;
  }
  body .table thead tr th.text-end {
    text-align: right;
  }
  body .table.table-detail-view {
    width: 50%;
  }
  body .card {
    margin-top: 6pt;
    padding: 6pt 0;
  }
  body .card .table th, body .card .table td {
    padding: 5pt 0;
    border: none;
  }
  body .card .table.table-grid-view th, body .card .table.table-grid-view td {
    padding: 5pt;
    border: 1pt solid #000;
  }
  body .card .table.table-grid-view thead tr th {
    text-align: center;
  }
  body .text-start {
    text-align: left !important;
  }
  body .text-center {
    text-align: center !important;
  }
  body .text-end {
    text-align: right !important;
  }
  body .border-bottom {
    border: 1px !important;
  }
  body .border-0 {
    border: 0 !important;
  }
  body .border-start-0 {
    border-left: 0 !important;
  }
  body .border-end-0 {
    border-right: 0 !important;
  }
  body .border-top-0 {
    border-top: 0 !important;
  }
  body .border-bottom-0 {
    border-bottom: 0 !important;
  }
  body .p-0 {
    padding: 0;
  }
  body .m-0 {
    margin: 0;
  }
  body .mt-1 {
    margin-top: 1rem !important;
  }
  body .mt-2 {
    margin-top: 1.5rem !important;
  }
  body .mt-3 {
    margin-top: 1.5rem !important;
  }
  body .mt-5 {
    margin-top: 2.5rem !important;
  }
  body .font-weight-bold {
    font-weight: bold;
  }
  body .text-nowrap {
    white-space: nowrap;
  }
}